/* base */
*,
*:before,
*:after {
  box-sizing: border-box; }

html {
  font-size: inherit;
  line-height: 1.3;
  font-size: 16px; }

html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: #ffffff;
  font-size: 15px;
  font-family: "DIN Pro";
  color: #232323;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: #232323;
  font-weight: bold;
  text-decoration: none;
  margin: 0 0 15px 0; }

.h1, h1 {
  font-size: 50px;
  line-height: 1.05;
  letter-spacing: -1px;
  margin-bottom: 50px;
  font-family: "GetVoIP Grotesque";
  font-style: italic; }

.h2, h2 {
  font-size: 40px;
  line-height: 1.14;
  margin-bottom: 20px;
  font-family: "GetVoIP Grotesque";
  font-style: italic; }

.h3, h3 {
  font-size: 16px;
  line-height: 1.38; }

.h4, h4 {
  font-size: 14px;
  line-height: 1.38; }

.h5, h5 {
  font-size: 13px; }

.h6, h6 {
  font-size: 12px; }

small, .small {
  font-size: 13px;
  line-height: 1.2; }

p {
  line-height: 26px;
  margin: 0 0 20px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: bold; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

a, .link {
  color: #57be41;
  text-decoration: none;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s; }
  a:hover, .link:hover {
    color: #57be41;
    text-decoration: none; }
  a:focus, a:active, .link:focus, .link:active {
    outline: none; }

a.link:hover {
  text-decoration: underline; }

img, .img {
  box-sizing: content-box;
  display: inline-block;
  vertical-align: middle; }

.container {
